import React from 'react';
import './imageLinkForm.css'

const ImageLinkForm = ({onInputChange, onButtonSubmit}) => {
	return  (
		<div>
			<p className='f3'>
				{'This magic app will detect faces in your pictures. Give it a try'}
			</p>
			<div className='center'>
				<div className='center  pa4 br3 shadow-5 form'>
					<input className='f4 pa3 w-70 center' type='text' onChange={onInputChange} />
					<button className='w-30 bn grow f4 link ph3 pv2 dib white bg-light-blue' onClick={onButtonSubmit} >Detect face</button>
				</div>
			</div>
		</div>
	)
}

export default ImageLinkForm;